// Config.js v3.4.1

(function() {
	//---------------------------------------------------------------------------
	// Config 
	//---------------------------------------------------------------------------
	var Config = (pzpr.Puzzle.prototype.Config = function(puzzle) {
		this.puzzle = puzzle;
		this.init();
	});
	Config.prototype = {
		list: null /*  */,

		//---------------------------------------------------------------------------
		// config.init()        
		// config.add()         
		//---------------------------------------------------------------------------
		init: function() {
			this.list = {};

			/*  */
			this.add("font", 1, {
				option: [1, 2]
			}); /*  1: 2: */
			this.add("cursor", true); /*  */
			this.add("trialmarker", true); /* show trial marker */
			this.add("irowake", false, { variety: true }); /*  */
			this.add("irowakeblk", false, { variety: true }); /*  */

			this.add("dispmove", true); /*  */
			this.add("disptype_yajilin", 1, {
				option: [1, 2]
			}); /* yajilin:  */
			this.add("disptype_bosanowa", 1, {
				option: [1, 2, 3]
			}); /* bosanowa:  */
			this.add("disptype_interbd", 3, {
				option: [1, 2, 3]
			}); /* interbd: Clue display mode */
			this.add("snakebd", false); /* hebi:  */
			this.add("dispqnumbg", false); /* yinyang:  */
			this.add("undefcell", true); /* shugaku:  */

			this.add("squarecell", true); /*  */

			/*  */
			this.add("use", !pzpr.env.API.touchevent ? 1 : 2, {
				option: [1, 2]
			}); /*  */
			this.add("use_tri", 1, {
				option: [1, 2, 3]
			}); /* shakashaka:  */
			this.add(
				"support_tri",
				true
			); /* shakashaka:  (for 2Cell) */

			this.add("bgcolor", false); /* slither  */
			this.add(
				"singlenum",
				!pzpr.env.API.touchevent
			); /* hanare:  */
			this.add("enline", true); /* kouchoku:  */
			this.add("lattice", true); /* kouchoku:  */

			/*  */
			this.add("autocmp", true, {
				variety: true
			}); /*  or kouchoku */
			this.add("autoerr", false, {
				variety: true
			}); /* hitori:, gokigen,wagiri: */

			/*  */
			this.add("multierr", false); /*  */
			this.add(
				"forceallcell",
				false
			); /* fillomino:  */

			/* puzzle variant rules; must defaut to false */
			this.add("dontpassallcell", false, {
				variant: true,
				volatile: true
			}); /* arukone: don't require passing all cells */
			this.add("aquarium_regions", false, {
				variant: true,
				volatile: true
			}); /* aquarium: Rule variation for disconnected cells in one region */
			this.add("country_empty", false, { variant: true, volatile: true });
			/* generic variant */
			this.add("variant", false, { variant: true, volatile: true });
			this.add("variantid", "", { volatile: true });

			/* EDITOR */
			this.add("discolor", false); /* tentaisho:  */
			/* () */
			this.add("uramashu", false, { volatile: true }); /*  */
		},
		add: function(name, defvalue, extoption) {
			if (!extoption) {
				extoption = {};
			}
			var item = {
				val: defvalue,
				defval: defvalue,
				volatile: !!extoption.volatile,
				extoption: extoption // stored for the benefit of ui.MenuConfig
			};
			if (!!extoption.option) {
				item.option = extoption.option;
			}
			if (!!extoption.variety) {
				item.variety = {};
			}
			if (!!extoption.variant) {
				item.variant = true;
			}
			this.list[name] = item;
		},

		//---------------------------------------------------------------------------
		// config.getCurrentName() config
		// config.getNormalizedName() Config@varietypid
		//---------------------------------------------------------------------------
		getCurrentName: function(name) {
			switch (name) {
				case "color_qanscolor":
					name = "color_shadecolor";
					break;
				case "autocmp_area":
					if (this.getexec("autocmp")) {
						name = "autocmp";
					}
					break;
				case "autocmp_border":
					if (this.getexec("autocmp")) {
						name = "autocmp";
					}
					break;
			}
			return name;
		},
		getNormalizedName: function(argname) {
			var info = { name: argname };
			if (argname.match(/\@/)) {
				var splitted = argname.split(/\@/);
				info.name = splitted[0];
				var pid = pzpr.variety.toPID(splitted[1]);
				if (!!pid) {
					info.pid = pid;
				}
			}
			info.name = this.getCurrentName(info.name);
			return info;
		},

		//---------------------------------------------------------------------------
		// config.get()  
		// config.set()  
		// config.reset()
		//---------------------------------------------------------------------------
		get: function(argname) {
			var names = this.getNormalizedName(argname),
				name = names.name;
			var item = this.list[name];
			if (!item) {
				return null;
			}
			if (!!item.variety) {
				var pid = names.pid !== void 0 ? names.pid : this.puzzle.pid;
				if (item.variety[pid] !== void 0) {
					return item.variety[pid];
				}
			}
			return item.val;
		},
		set: function(argname, newval) {
			var names = this.getNormalizedName(argname),
				name = names.name;
			if (!this.list[name]) {
				return;
			}
			newval = this.setproper(names, newval);
			this.configevent(name, newval);
			this.puzzle.emit("config", name, newval);
		},
		reset: function(argname) {
			var names = this.getNormalizedName(argname);
			var item = this.list[names.name];
			if (!item) {
				return;
			}
			if (!!item.variety && !names.pid) {
				item.variety = {};
				this.set(names.name, item.defval);
			} else {
				this.set(argname, item.defval);
			}
		},

		//---------------------------------------------------------------------------
		// config.getList()  
		//---------------------------------------------------------------------------
		getList: function() {
			var conf = {};
			for (var idname in this.list) {
				if (this.getexec(idname)) {
					conf[idname] = this.get(idname);
				}
			}
			return conf;
		},

		getvariant: function(name) {
			var item = this.list[name];
			if (!item) {
				return null;
			}
			return item.variant;
		},
		getVariants: function() {
			var conf = {};
			for (var key in this.list) {
				var item = this.list[key];
				if (!item.variant) {
					continue;
				}
				if (this.getexec(key)) {
					conf[key] = this.get(key);
				}
			}
			return conf;
		},

		//---------------------------------------------------------------------------
		// config.getAll()  
		// config.setAll()  
		//---------------------------------------------------------------------------
		getAll: function() {
			var object = {};
			for (var key in this.list) {
				var item = this.list[key];
				if (item.volatile) {
					continue;
				}
				if (item.val !== item.defval) {
					object[key] = item.val;
				}
				if (!item.variety) {
					continue;
				}
				for (var pid in item.variety) {
					if (item.variety[pid] !== item.defval) {
						object[key + "@" + pid] = item.variety[pid];
					}
				}
			}
			return object;
		},
		setAll: function(settings) {
			this.init();
			for (var key in settings) {
				this.set(key, settings[key]);
			}
		},

		//---------------------------------------------------------------------------
		// config.setproper()    
		//---------------------------------------------------------------------------
		setproper: function(names, newval) {
			var name = names.name;
			var item = this.list[name];
			switch (typeof item.defval) {
				case "boolean":
					newval = !!newval;
					break;
				case "number":
					newval = +newval;
					break;
				case "string":
					newval = "" + newval;
					break;
			}
			if (!item.option || item.option.indexOf(newval) >= 0) {
				if (!!item.variety) {
					var pid = names.pid !== void 0 ? names.pid : this.puzzle.pid;
					if (!!pid) {
						item.variety[pid] = newval;
					}
				} else {
					item.val = newval;
				}
			}
			return newval;
		},

		//---------------------------------------------------------------------------
		// config.getexec()  
		//---------------------------------------------------------------------------
		getexec: function(name) {
			var puzzle = this.puzzle,
				pid = puzzle.pid,
				EDITOR = !puzzle.playeronly,
				exec = false;
			switch (name) {
				case "use":
					exec = puzzle.mouse.use;
					break;
				case "use_tri":
					exec = pid === "shakashaka";
					break;
				case "support_tri":
					exec = pid === "shakashaka";
					break;
				case "dispmove":
					exec = puzzle.board.linegraph.moveline;
					break;
				case "disptype_bosanowa":
					exec = pid === "bosanowa";
					break;
				case "disptype_yajilin":
					exec = pid === "yajilin";
					break;
				case "disptype_interbd":
					exec = pid === "interbd";
					break;
				case "bgcolor":
					exec = pid === "slither";
					break;
				case "irowake":
					exec = puzzle.painter.irowake;
					break;
				case "irowakeblk":
					exec = puzzle.painter.irowakeblk;
					break;
				case "snakebd":
					exec = pid === "hebi";
					break;
				case "dispqnumbg":
					exec = pid === "yinyang";
					break;
				case "undefcell":
					exec = pid === "shugaku";
					break;
				case "autocmp":
					exec = !!puzzle.painter.autocmp;
					break;
				case "autoerr":
					exec = pid === "hitori" || pid === "gokigen" || pid === "wagiri";
					break;
				case "singlenum":
					exec = pid === "hanare" || pid === "putteria";
					break;
				case "enline":
				case "lattice":
					exec = pid === "kouchoku" || pid === "angleloop";
					break;
				case "discolor":
					exec = EDITOR && pid === "tentaisho";
					break;
				case "uramashu":
					exec = pid === "mashu";
					break;
				case "forceallcell":
					exec = pid === "fillomino" || pid === "symmarea";
					break;
				case "dontpassallcell":
					exec = pid === "arukone";
					break;
				case "aquarium_regions":
					exec = pid === "aquarium";
					break;
				case "country_empty":
					exec = pid === "country";
					break;
				default:
					exec = !!this.list[name];
			}
			return exec;
		},

		//---------------------------------------------------------------------------
		// config.configevent()  
		//---------------------------------------------------------------------------
		configevent: function(name, newval) {
			var puzzle = this.puzzle;
			if (!puzzle.klass || !this.getexec(name)) {
				return;
			}
			switch (name) {
				case "irowake":
				case "irowakeblk":
				case "dispmove":
				case "cursor":
				case "trialmarker":
				case "undefcell":
				case "autocmp":
				case "autoerr":
				case "aquarium_regions":
				case "snakebd":
				case "disptype_yajilin":
				case "disptype_interbd":
				case "dispqnumbg":
					puzzle.redraw();
					break;

				case "font":
					puzzle.painter.initFont();
					puzzle.redraw();
					break;

				case "multierr":
					puzzle.checker.resetCache();
					break;

				case "disptype_bosanowa":
					puzzle.setCanvasSizeByCellSize(); /*  */
					break;

				case "uramashu":
					puzzle.board.revCircleConfig(newval);
					puzzle.redraw();
					break;
			}
		}
	};
})();
