// PieceList.js v3.4.1

pzpr.classmgr.makeCommon({
	//----------------------------------------------------------------------------
	// PieceList 
	//---------------------------------------------------------------------------
	PieceList: {
		initialize: function(list) {
			if (!!list) {
				this.extend(list);
			}
		},

		length: 0,

		//--------------------------------------------------------------------------------
		// Array
		// list.add()      (push())
		// list.extend()   PieceList
		// list.pop()      
		//--------------------------------------------------------------------------------
		add: Array.prototype.push,
		extend: function(list) {
			var len = list.length,
				n = this.length;
			this.length += len;
			for (var i = 0; i < len; i++) {
				this[n + i] = list[i];
			}
		},
		pop: Array.prototype.pop,

		//--------------------------------------------------------------------------------
		// Arrayiterator
		// list.each()     
		// list.some()     true
		//--------------------------------------------------------------------------------
		each: Array.prototype.forEach,
		some: Array.prototype.some,

		//--------------------------------------------------------------------------------
		// list.filter()   trueclist
		// list.notnull()  nullclist
		//--------------------------------------------------------------------------------
		/* constructorArray.prototype */
		filter: function(cond) {
			var list = new this.constructor(),
				len = this.length,
				n = 0;
			for (var i = 0; i < len; i++) {
				if (cond(this[i])) {
					list[n] = this[i];
					n++;
				}
			}
			list.length = n;
			return list;
		},
		notnull: function(cond) {
			return this.filter(function(piece) {
				return !piece.isnull;
			});
		},

		//--------------------------------------------------------------------------------
		// list.map()      clistclist
		//--------------------------------------------------------------------------------
		/* constructorArray.prototype */
		map: function(changer) {
			var list = new this.constructor(),
				len = (list.length = this.length);
			for (var i = 0; i < len; i++) {
				list[i] = changer(this[i]);
			}
			return list;
		},

		//--------------------------------------------------------------------------------
		// list.indexOf()  
		// list.include()  
		// list.remove()   
		//--------------------------------------------------------------------------------
		indexOf: Array.prototype.indexOf,
		include: function(target) {
			return this.indexOf(target) >= 0;
		},
		remove: function(piece) {
			var idx = this.indexOf(piece);
			if (idx >= 0) {
				Array.prototype.splice.call(this, idx, 1);
			}
		},

		//--------------------------------------------------------------------------------
		// list.seterr()   error
		// list.setnoerr() error=-1
		// list.setinfo()  qinfo
		//--------------------------------------------------------------------------------
		seterr: function(num) {
			if (!this.board.isenableSetError()) {
				return;
			}
			for (var i = 0; i < this.length; i++) {
				this[i].error = num;
			}
		},
		setnoerr: function() {
			if (!this.board.isenableSetError()) {
				return;
			}
			for (var i = 0; i < this.length; i++) {
				if (this[i].error === 0) {
					this[i].error = -1;
				}
			}
		},
		setinfo: function(num) {
			for (var i = 0; i < this.length; i++) {
				this[i].qinfo = num;
			}
		},

		//---------------------------------------------------------------------------
		// list.allclear() ,
		// list.ansclear() qans,anum,line,qsub,error
		// list.subclear() qsub,error
		// list.errclear() error
		// list.trialclear() Trial
		// list.propclear() 4
		//---------------------------------------------------------------------------
		/* undo,redo, isrec===true */
		allclear: function(isrec) {
			this.propclear(["ques", "ans", "sub", "info"], isrec);
		},
		ansclear: function() {
			this.propclear(["ans", "sub", "info"], true);
		},
		subclear: function() {
			this.propclear(["sub", "info"], true);
		},
		errclear: function() {
			this.propclear(["info"], false);
		},
		trialclear: function() {
			this.propclear(["trial"], false);
		},
		propclear: function(target, isrec) {
			var props = [],
				norec = {};
			if (this.length > 0) {
				props = this[0].getproplist(target);
				norec = this[0].propnorec;
			}
			for (var i = 0; i < this.length; i++) {
				var piece = this[i];
				for (var j = 0; j < props.length; j++) {
					var pp = props[j],
						pos = "";
					if (pp.length > 4 && pp.substr(0, 4) === "snum") {
						pos = pp.charAt(4);
						pp = pp.substr(0, 4);
						var def = piece.constructor.prototype[pp];
						if (piece[pp][pos] !== def) {
							if (isrec && !norec[pp]) {
								piece.addOpe(pp + pos, piece[pp][pos], def);
							}
							piece[pp][pos] = def;
						}
					} else {
						var def = piece.constructor.prototype[pp];
						if (piece[pp] !== def) {
							if (isrec && !norec[pp]) {
								piece.addOpe(pp, piece[pp], def);
							}
							piece[pp] = def;
						}
					}
				}
			}
		}
	},

	//----------------------------------------------------------------------------
	// CellList Cell
	//---------------------------------------------------------------------------
	"CellList:PieceList": {
		//---------------------------------------------------------------------------
		// clist.getRectSize()  Cell
		//---------------------------------------------------------------------------
		getRectSize: function() {
			var bd = this.board;
			var d = {
				x1: bd.maxbx + 1,
				x2: bd.minbx - 1,
				y1: bd.maxby + 1,
				y2: bd.minby - 1,
				cols: 0,
				rows: 0,
				cnt: 0
			};
			for (var i = 0; i < this.length; i++) {
				var cell = this[i];
				if (d.x1 > cell.bx) {
					d.x1 = cell.bx;
				}
				if (d.x2 < cell.bx) {
					d.x2 = cell.bx;
				}
				if (d.y1 > cell.by) {
					d.y1 = cell.by;
				}
				if (d.y2 < cell.by) {
					d.y2 = cell.by;
				}
				d.cnt++;
			}
			d.cols = (d.x2 - d.x1 + 2) / 2;
			d.rows = (d.y2 - d.y1 + 2) / 2;
			return d;
		},

		//--------------------------------------------------------------------------------
		// clist.getQnumCell()  Clist
		//--------------------------------------------------------------------------------
		getQnumCell: function() {
			for (var i = 0, len = this.length; i < len; i++) {
				if (this[i].isNum()) {
					return this[i];
				}
			}
			return this.board.emptycell;
		},

		includes: function(cell) {
			for (var i = 0, len = this.length; i < len; i++) {
				if (this[i] === cell) {
					return true;
				}
			}
			return false;
		},

		//--------------------------------------------------------------------------------
		// clist.getTopCell()  Clist
		//--------------------------------------------------------------------------------
		getTopCell: function() {
			var bd = this.board,
				tcell = null,
				bx = bd.maxbx,
				by = bd.maxby;
			for (var i = 0; i < this.length; i++) {
				var cell = this[i];
				if (cell.bx > bx || (cell.bx === bx && cell.by >= by)) {
					continue;
				}
				tcell = this[i];
				bx = cell.bx;
				by = cell.by;
			}
			return tcell;
		},

		//---------------------------------------------------------------------------
		// clist.eraseLines()  Clistline
		//---------------------------------------------------------------------------
		eraseLines: function() {
			var count = 0;
			for (var i = 0, len = this.length; i < len; i++) {
				for (var j = i + 1; j < len; j++) {
					var border = this.puzzle.mouse.getnb(
						this[i].getaddr(),
						this[j].getaddr()
					);
					if (!border.isnull) {
						border.removeLine();
						count++;
					}
				}
			}
			if (count > 0) {
				this.draw();
			}
		},

		//---------------------------------------------------------------------------
		// clist.draw()   
		//---------------------------------------------------------------------------
		draw: function() {
			var d = this.getRectSize();
			this.puzzle.painter.paintRange(d.x1 - 1, d.y1 - 1, d.x2 + 1, d.y2 + 1);
		}
	},

	//----------------------------------------------------------------------------
	// CrossList Cross
	//---------------------------------------------------------------------------
	"CrossList:PieceList": {},

	//----------------------------------------------------------------------------
	// BorderList Border
	//---------------------------------------------------------------------------
	"BorderList:PieceList": {
		//---------------------------------------------------------------------------
		// blist.cellinside()  
		// blist.crossinside() 
		//---------------------------------------------------------------------------
		cellinside: function() {
			var clist = new this.klass.CellList(),
				pushed = [];
			for (var i = 0; i < this.length; i++) {
				var border = this[i],
					cell1 = border.sidecell[0],
					cell2 = border.sidecell[1];
				if (!cell1.isnull && pushed[cell1.id] !== true) {
					clist.add(cell1);
					pushed[cell1.id] = true;
				}
				if (!cell2.isnull && pushed[cell2.id] !== true) {
					clist.add(cell2);
					pushed[cell2.id] = true;
				}
			}
			return clist;
		},
		crossinside: function() {
			var clist = new this.klass.CrossList(),
				pushed = [];
			for (var i = 0; i < this.length; i++) {
				var border = this[i],
					cross1 = border.sidecross[0],
					cross2 = border.sidecross[1];
				if (!cross1.isnull && pushed[cross1.id] !== true) {
					clist.add(cross1);
					pushed[cross1.id] = true;
				}
				if (!cross2.isnull && pushed[cross2.id] !== true) {
					clist.add(cross2);
					pushed[cross2.id] = true;
				}
			}
			return clist;
		}
	},

	//----------------------------------------------------------------------------
	// ExCellList ExCell
	//---------------------------------------------------------------------------
	"ExCellList:PieceList": {}
});
