// Piece.js v3.4.1

pzpr.classmgr.makeCommon({
	//---------------------------------------------------------------------------
	// BoardPiece Cell, Cross, Border, ExCell
	//---------------------------------------------------------------------------
	"BoardPiece:Position": {
		group: "none",
		id: null,
		isnull: true,

		// 
		/*  */
		ques: 0, // cell  :(1: 2-5: 6: 7: 8:
		//         11-17: 21-22: 31:Hole 41-42: 51:)
		// cross :()
		// border:()
		qdir: 0, // cell  :()
		// border:(/)
		qnum: -1, // cell  :(////)
		// cross :()
		// border:(/)
		qnum2: -1, // cell  :(/)
		qnum3: -1,
		qnum4: -1,

		qchar: 0, // excell:

		/*  */
		qans: 0, // cell  :(1:/ 2-5: 11-13: 31-32: 41-50:)
		// border:()
		anum: -1, // cell  :(//)
		line: 0, // border:()

		/*  */
		qsub: 0, // cell  :(1: 1-2:/ 3:)
		// border:(1: 2: 11-14:)
		qcmp: 0, // cell  :(1:cmp 1-2:)
		snum: -1, // cell  :

		/*  */
		error: 0,
		qinfo: 0,
		trial: 0, // TrialModestate

		propques: ["ques", "qdir", "qnum", "qnum2", "qchar"],
		propans: ["qans", "anum", "line", "trial"],
		propsub: ["qsub", "qcmp", "snum"],
		propinfo: ["error", "qinfo"],
		propnorec: { color: 1, error: 1, qinfo: 1 },

		// 
		maxnum: 999,
		minnum: 1,

		//---------------------------------------------------------------------------
		// initAdjacent()   
		// initAdjBorder()  
		//---------------------------------------------------------------------------
		initAdjacent: function() {
			this.adjacent = {
				top: this.relobj(0, -2),
				bottom: this.relobj(0, 2),
				left: this.relobj(-2, 0),
				right: this.relobj(2, 0)
			};
		},
		initAdjBorder: function() {
			this.adjborder = {
				top: this.relbd(0, -1),
				bottom: this.relbd(0, 1),
				left: this.relbd(-1, 0),
				right: this.relbd(1, 0)
			};
		},

		//---------------------------------------------------------------------------
		// getter/setter
		//---------------------------------------------------------------------------
		setQues: function(val) {
			this.setdata("ques", val);
		},
		setQans: function(val) {
			this.setdata("qans", val);
		},
		setQdir: function(val) {
			this.setdata("qdir", val);
		},
		setQnum: function(val) {
			this.setdata("qnum", val);
		},
		setQnum2: function(val) {
			this.setdata("qnum2", val);
		},
		setQchar: function(val) {
			this.setdata("qchar", val);
		},
		setAnum: function(val) {
			this.setdata("anum", val);
		},
		setLineVal: function(val) {
			this.setdata("line", val);
		},
		setQsub: function(val) {
			this.setdata("qsub", val);
		},
		setQcmp: function(val) {
			this.setdata("qcmp", val);
		},
		setSnum: function(val) {
			this.setdata("snum", val);
		},

		setQnumDir: function(dir, val) {
			switch (dir) {
				case this.RT:
					this.setdata("qnum", val);
					return;
				case this.DN:
					this.setdata("qnum2", val);
					return;
				case this.LT:
					this.setdata("qnum3", val);
					return;
				case this.UP:
					this.setdata("qnum4", val);
					return;
			}
		},
		getQnumDir: function(dir) {
			switch (dir) {
				case this.RT:
					return this.qnum;
				case this.DN:
					return this.qnum2;
				case this.LT:
					return this.qnum3;
				case this.UP:
					return this.qnum4;
			}
			return -1;
		},

		//---------------------------------------------------------------------------
		// setdata() Cell,Cross,Border,ExCell
		// addOpe()  
		//---------------------------------------------------------------------------
		setdata: function(prop, num, force) {
			if (this[prop] === num) {
				return;
			}
			if (!!this.prehook[prop]) {
				// when force is set (undo, redo), bypass the check
				// but still execute the prehook for side-effects
				if (this.prehook[prop].call(this, num) && !force) {
					return;
				}
			}

			this.addOpe(prop, this[prop], num);
			this[prop] = num;

			var trialstage = this.board.trialstage;
			if (trialstage > 0) {
				this.trial = trialstage;
			}

			this.board.modifyInfo(this, this.group + "." + prop);

			if (!!this.posthook[prop]) {
				this.posthook[prop].call(this, num);
			}
		},
		addOpe: function(property, old, num) {
			if (old === num) {
				return;
			}
			this.puzzle.opemgr.add(
				new this.klass.ObjectOperation(this, property, old, num)
			);
		},

		//---------------------------------------------------------------------------
		// setdata2() Cell,Cross,Border,ExCellpos
		//---------------------------------------------------------------------------
		setdata2: function(prop, pos, num, bypassPrehook) {
			if (this[prop][pos] === num) {
				return;
			}
			if (!!this.prehook[prop]) {
				if (this.prehook[prop].call(this, pos, num) && !bypassPrehook) {
					return;
				}
			}

			this.addOpe(prop + pos, this[prop][pos], num);
			this[prop][pos] = num;

			var trialstage = this.board.trialstage;
			if (trialstage > 0) {
				this.trial = trialstage;
			}

			if (!!this.posthook[prop]) {
				this.posthook[prop].call(this, pos, num);
			}
		},

		//---------------------------------------------------------------------------
		// getprops() 
		// compare()  callback
		//---------------------------------------------------------------------------
		getprops: function() {
			var props = {};
			var proplist = this.getproplist(["ques", "ans", "sub"]);
			for (var i = 0; i < proplist.length; i++) {
				var a = proplist[i];
				props[a] = this[a];
			}
			return props;
		},
		compare: function(props, callback) {
			var proplist = this.getproplist(["ques", "ans", "sub"]);
			for (var i = 0; i < proplist.length; i++) {
				var a = proplist[i];
				if (props[a] !== this[a]) {
					callback(this.group, this.id, a);
				}
			}
		},

		//---------------------------------------------------------------------------
		// getproplist() ansclear
		//---------------------------------------------------------------------------
		getproplist: function(types) {
			var array = [];
			for (var i = 0; i < types.length; i++) {
				var array1 = [];
				switch (types[i]) {
					case "ques":
						array1 = this.propques;
						break;
					case "ans":
						array1 = this.propans;
						break;
					case "sub":
						array1 = this.propsub;
						break;
					case "info":
						array1 = this.propinfo;
						break;
					case "trial":
						array1 = ["trial"];
						break;
				}
				array = array.concat(array1);
			}
			if (array.indexOf("snum") >= 0 && this.enableSubNumberArray) {
				array.splice(
					array.indexOf("snum"),
					1,
					"snum0",
					"snum1",
					"snum2",
					"snum3"
				);
			}
			return array;
		},

		//---------------------------------------------------------------------------
		// getmaxnum() 
		// getminnum() 
		//---------------------------------------------------------------------------
		getmaxnum: function() {
			return typeof this.maxnum === "function" ? this.maxnum() : this.maxnum;
		},
		getminnum: function() {
			return typeof this.minnum === "function" ? this.minnum() : this.minnum;
		},

		//---------------------------------------------------------------------------
		// prehook  
		// posthook 
		//---------------------------------------------------------------------------
		prehook: {},
		posthook: {},

		//---------------------------------------------------------------------------
		// seterr()  error
		// setinfo() qinfo
		//---------------------------------------------------------------------------
		seterr: function(num) {
			if (this.board.isenableSetError()) {
				this.error = num;
			}
		},
		setinfo: function(num) {
			this.qinfo = num;
		}
	},

	//---------------------------------------------------------------------------
	// Cell BoardCell
	//---------------------------------------------------------------------------
	// (1)
	// Cell
	"Cell:BoardPiece": {
		group: "cell",

		lcnt: 0, // 
		base: null, //  ()
		dirs51: 2,

		disInputHatena: false, // qnum==-2

		numberWithMB: false, // ()
		numberAsObject: false, // qnum/anum()
		numberAsLetter: false, // 

		numberRemainsUnshaded: false, // 
		enableSubNumberArray: false, // 
		supportQnumAnum: false, // True if qnum and anum can appear at the same time.

		adjacent: {}, // 
		adjborder: {}, // 

		initialize: function() {
			this.temp = this.constructor.prototype;
			if (this.enableSubNumberArray) {
				var anum0 = this.temp.anum;
				this.snum = [anum0, anum0, anum0, anum0];
			}
		},

		//---------------------------------------------------------------------------
		// prehook  
		// posthook 
		//---------------------------------------------------------------------------
		prehook: {
			qnum: function(num) {
				return this.getminnum() > 0 && num === 0;
			},
			qnum2: function(num) {
				return this.getminnum() > 0 && num === 0;
			},
			anum: function(num) {
				return this.getminnum() > 0 && num === 0;
			}
		},
		posthook: {},

		//---------------------------------------------------------------------------
		// cell.isShade()   Cell
		// cell.isUnshade() Cell
		// cell.setShade()  Cell
		// cell.clrShade()  Cell
		//---------------------------------------------------------------------------
		isShade: function() {
			return !this.isnull && this.qans === 1;
		},
		isUnshade: function() {
			return !this.isnull && this.qans !== 1;
		},
		setShade: function() {
			this.setQans(1);
		},
		clrShade: function() {
			this.setQans(0);
		},

		// disallow certain inputs
		allowShade: function() {
			if (this.numberRemainsUnshaded) {
				return this.qnum === -1;
			}
			return true;
		},
		allowUnshade: function() {
			if (this.numberRemainsUnshaded) {
				return this.qnum === -1 || this.puzzle.painter.enablebcolor;
			}
			return true;
		},

		// is cell shading status decided for the purpose of auto-answer check
		isShadeDecided: function() {
			if (this.isnull) {
				return true;
			}
			if (this.isShade()) {
				return true;
			}
			if (this.qsub > 0) {
				return true;
			}
			if (!this.allowShade() || !this.allowUnshade()) {
				return true;
			}
			return false;
		},

		//-----------------------------------------------------------------------
		// cell.getNum()     Cell
		// cell.setNum()     Cell
		//-----------------------------------------------------------------------
		getNum: function() {
			return this.qnum !== -1 ? this.qnum : this.anum;
		},
		setNum: function(val) {
			if (this.getminnum() > 0 && val === 0) {
				return;
			}
			// editmode val>=0 val=-1 val=-2
			if (this.puzzle.editmode) {
				val =
					(this.numberAsObject || val === -2) && this.qnum === val ? -1 : val;
				this.setQnum(val);
				this.setAnum(-1);
				if (this.numberRemainsUnshaded) {
					this.setQans(0);
				}
				if (!this.puzzle.painter.enablebcolor) {
					this.setQsub(0);
				}
				this.setQcmp(0);
				this.clrSnum();
			}
			// playmode val>=0 val=-1 numberAsObjectval=-2 numberWithMBval=-2,-3
			else if (this.qnum === -1) {
				var vala =
					val > -1 && !(this.numberAsObject && this.anum === val) ? val : -1;
				var vals =
					val < -1 && !(this.numberAsObject && this.qsub === -val - 1)
						? -val - 1
						: 0;
				this.setAnum(vala);
				this.setQsub(vals);
				this.setQdir(0);
				this.setQcmp(0);
				if (!(this.numberWithMB && vala === -1)) {
					this.clrSnum();
				}
			}
		},

		//-----------------------------------------------------------------------
		// cell.isNum()       Cell
		// cell.noNum()       Cell
		// cell.isValidNum()  Cell0
		// cell.isNumberObj() Cellor
		// cell.sameNumber()  Cell
		//-----------------------------------------------------------------------
		isNum: function() {
			return (
				!this.isnull &&
				(this.qnum !== this.temp.qnum || this.anum !== this.temp.anum)
			);
		},
		noNum: function() {
			return (
				!this.isnull &&
				this.qnum === this.temp.qnum &&
				this.anum === this.temp.anum
			);
		},
		isValidNum: function() {
			return (
				!this.isnull &&
				(this.qnum >= 0 || (this.anum >= 0 && this.qnum === this.temp.qnum))
			);
		},
		isNumberObj: function() {
			return (
				this.qnum !== this.temp.qnum ||
				this.anum !== this.temp.anum ||
				(this.numberWithMB && this.qsub === 1)
			);
		},
		sameNumber: function(cell) {
			return this.isValidNum() && this.getNum() === cell.getNum();
		},

		//---------------------------------------------------------------------------
		// cell.setSnum() Cell
		// cell.clrSnum() Cell
		//---------------------------------------------------------------------------
		setSnum: function(pos, num) {
			if (this.isNum() && num !== -1) {
				return;
			}
			if (!this.enableSubNumberArray) {
				this.setdata("snum", num); // 1
			} else {
				this.setdata2("snum", pos, num);
			}
		},
		clrSnum: function() {
			if (!this.enableSubNumberArray) {
				this.setSnum(-1);
			} else {
				this.setSnum(0, -1);
				this.setSnum(1, -1);
				this.setSnum(2, -1);
				this.setSnum(3, -1);
			}
		},

		//---------------------------------------------------------------------------
		// cell.is51cell()     []()
		// cell.set51cell()    []()
		// cell.remove51cell() []()
		//---------------------------------------------------------------------------
		// 
		is51cell: function() {
			return this.ques === 51;
		},
		set51cell: function(val) {
			this.setQues(51);
			for (var dir = 1; dir <= 4; dir++) {
				this.setQnumDir(dir, -1);
			}
			this.setAnum(-1);
		},
		remove51cell: function(val) {
			this.setQues(0);
			for (var dir = 1; dir <= 4; dir++) {
				this.setQnumDir(dir, -1);
			}
			this.setAnum(-1);
		},

		//---------------------------------------------------------------------------
		// cell.ice() 
		//---------------------------------------------------------------------------
		ice: function() {
			return this.ques === 6;
		},

		isDot: function() {
			return this.qsub === 1;
		},

		//---------------------------------------------------------------------------
		// cell.isEmpty() / cell.isValid() 
		//---------------------------------------------------------------------------
		isEmpty: function() {
			return this.isnull || this.ques === 7;
		},
		isValid: function() {
			return !this.isnull && this.ques !== 7;
		},

		//---------------------------------------------------------------------------
		// cell.isDeparture()   
		// cell.isDestination() 
		// isDestinationtrue
		//---------------------------------------------------------------------------
		isDeparture: function() {
			return !this.isnull && !!this.base && this.isNum();
		},
		isDestination: function() {
			return !this.isnull && !!this.base && !this.base.isnull;
		},

		//---------------------------------------------------------------------------
		// cell.isLineStraight()   
		// cell.isLineCurve()      
		//---------------------------------------------------------------------------
		isLineStraight: function() {
			// 0: 1: 2:
			if (
				this.lcnt === 2 &&
				this.adjborder.top.isLine() &&
				this.adjborder.bottom.isLine()
			) {
				return 1;
			} else if (
				this.lcnt === 2 &&
				this.adjborder.left.isLine() &&
				this.adjborder.right.isLine()
			) {
				return 2;
			}
			return 0;
		},
		isLineCurve: function() {
			return this.lcnt === 2 && !this.isLineStraight();
		},

		//---------------------------------------------------------------------------
		// cell.isLP()  
		// cell.noLP()  
		//---------------------------------------------------------------------------
		// 
		isLPobj: {
			1: { 11: 1, 12: 1, 14: 1, 15: 1 } /* UP */,
			2: { 11: 1, 12: 1, 16: 1, 17: 1 } /* DN */,
			3: { 11: 1, 13: 1, 15: 1, 16: 1 } /* LT */,
			4: { 11: 1, 13: 1, 14: 1, 17: 1 } /* RT */
		},
		noLPobj: {
			1: { 1: 1, 4: 1, 5: 1, 13: 1, 16: 1, 17: 1, 21: 1 } /* UP */,
			2: { 1: 1, 2: 1, 3: 1, 13: 1, 14: 1, 15: 1, 21: 1 } /* DN */,
			3: { 1: 1, 2: 1, 5: 1, 12: 1, 14: 1, 17: 1, 22: 1 } /* LT */,
			4: { 1: 1, 3: 1, 4: 1, 12: 1, 15: 1, 16: 1, 22: 1 } /* RT */
		},

		isLP: function(dir) {
			return !!this.isLPobj[dir][this.ques];
		},
		// ans.checkenableLinePartsnoLP()
		noLP: function(dir) {
			return !!this.noLPobj[dir][this.ques];
		},

		//---------------------------------------------------------------------------
		// cell.countDir4Cell()  4func==true
		//---------------------------------------------------------------------------
		countDir4Cell: function(func) {
			var adc = this.adjacent,
				cnt = 0;
			var cells = [adc.top, adc.bottom, adc.left, adc.right];
			for (var i = 0; i < 4; i++) {
				if (cells[i].group === "cell" && !cells[i].isnull && func(cells[i])) {
					cnt++;
				}
			}
			return cnt;
		},

		//---------------------------------------------------------------------------
		// cell.getdir4clist()   4
		// cell.getdir4cblist()  4
		//---------------------------------------------------------------------------
		getdir4clist: function() {
			var adc = this.adjacent,
				list = [];
			var cells = [adc.top, adc.bottom, adc.left, adc.right];
			for (var i = 0; i < 4; i++) {
				if (cells[i].group === "cell" && !cells[i].isnull) {
					list.push([cells[i], i + 1]);
				} /* i+1==dir */
			}
			return list;
		},
		getdir4cblist: function() {
			var adc = this.adjacent,
				adb = this.adjborder,
				cblist = [];
			var cells = [adc.top, adc.bottom, adc.left, adc.right];
			var bds = [adb.top, adb.bottom, adb.left, adb.right];
			for (var i = 0; i < 4; i++) {
				if ((cells[i].group === "cell" && !cells[i].isnull) || !bds[i].isnull) {
					cblist.push([cells[i], bds[i], i + 1]);
				} /* i+1==dir */
			}
			return cblist;
		},

		//------------------------------------------------------------------------------------
		// cell.getOrthogonalCell()  Get an adjacent cell in the direction of the target cell.
		//------------------------------------------------------------------------------------
		getOrthogonalCell: function(target) {
			if (this.bx === target.bx) {
				if (this.by === target.by) {
					return this.board.emptycell;
				}
				return this.by > target.by ? this.adjacent.top : this.adjacent.bottom;
			} else if (this.by === target.by) {
				return this.bx > target.bx ? this.adjacent.left : this.adjacent.right;
			}

			return this.board.emptycell;
		},

		//--------------------------------------------------------------------------------
		// cell.eraseMovedLines()  
		//--------------------------------------------------------------------------------
		eraseMovedLines: function() {
			if (this.path === null) {
				return;
			}
			var clist = this.path.clist,
				count = 0;
			for (var i = 0, len = clist.length; i < len; i++) {
				for (var j = i + 1; j < len; j++) {
					var border = clist[i].getnb(clist[j]);
					if (!border.isnull) {
						border.removeLine();
						count++;
					}
				}
			}
			if (count > 0) {
				clist.draw();
			}
		}
	},

	//---------------------------------------------------------------------------
	// Cross BoardCross(hascross>=1)
	//---------------------------------------------------------------------------
	// (2)
	// Cross
	"Cross:BoardPiece": {
		group: "cross",

		lcnt: 0, // 

		adjborder: {}, // 

		//-----------------------------------------------------------------------
		// cross.getNum()     Cross
		// cross.setNum()     Cross
		// cross.noNum()      Cross
		//-----------------------------------------------------------------------
		getNum: function() {
			return this.qnum;
		},
		setNum: function(val) {
			val = val === -2 && this.qnum === val ? -1 : val;
			this.setQnum(val);
		},
		noNum: function() {
			return !this.isnull && this.qnum === -1;
		},

		//---------------------------------------------------------------------------
		// cross.setCrossBorderError() Border
		//---------------------------------------------------------------------------
		setCrossBorderError: function() {
			this.seterr(1);
			this.board
				.borderinside(this.bx - 1, this.by - 1, this.bx + 1, this.by + 1)
				.seterr(1);
		}
	},

	//---------------------------------------------------------------------------
	// Border BoardBorder(hasborder>0)
	//---------------------------------------------------------------------------
	// (3)
	// Border
	"Border:BoardPiece": {
		initialize: function() {
			this.sidecell = [null, null]; // 
			this.sidecross = [null, null]; // 
			this.sideobj = []; // LineManager
		},
		group: "border",

		isvert: false, // true:() false:()
		inside: false, // true: false:or

		path: null, // Line

		// isLineNG
		enableLineNG: false,

		//---------------------------------------------------------------------------
		// initSideObject() 
		//---------------------------------------------------------------------------
		initSideObject: function() {
			var allowexcell =
				this.board.hasborder === 2 && this.board.hasexcell === 2;
			if (this.isvert) {
				this.sidecell[0] =
					!allowexcell || this.bx > 0
						? this.relcell(-1, 0)
						: this.relexcell(-1, 0);
				this.sidecell[1] =
					!allowexcell || this.bx < this.board.cols * 2
						? this.relcell(1, 0)
						: this.relexcell(1, 0);
				this.sidecross[0] = this.relcross(0, -1);
				this.sidecross[1] = this.relcross(0, 1);
			} else {
				this.sidecell[0] =
					!allowexcell || this.by > 0
						? this.relcell(0, -1)
						: this.relexcell(0, -1);
				this.sidecell[1] =
					!allowexcell || this.by < this.board.rows * 2
						? this.relcell(0, 1)
						: this.relexcell(0, 1);
				this.sidecross[0] = this.relcross(-1, 0);
				this.sidecross[1] = this.relcross(1, 0);
			}

			// LineManager
			this.sideobj = !this.board.borderAsLine ? this.sidecell : this.sidecross;
		},

		//---------------------------------------------------------------------------
		// prehook  
		// posthook 
		//---------------------------------------------------------------------------
		prehook: {
			qans: function(num) {
				return this.ques !== 0;
			},
			line: function(num) {
				return this.checkStableLine(num);
			}
		},
		posthook: {},

		//---------------------------------------------------------------------------
		// border.draw()  (Border)
		//---------------------------------------------------------------------------
		draw: function() {
			this.puzzle.painter.paintRange(
				this.bx - 2,
				this.by - 2,
				this.bx + 2,
				this.by + 2
			);
		},

		//-----------------------------------------------------------------------
		// border.isLine()      Borderline
		// border.setLine()     Border
		// border.setPeke()     Border
		// border.removeLine()  Border
		// border.removeLineAndQsub()  removes line and qsub
		//-----------------------------------------------------------------------
		isLine: function() {
			return this.line > 0;
		},
		setLine: function(id) {
			this.setLineVal(1);
			if (this.qsub === 2) {
				this.setQsub(0);
			}
		},
		setPeke: function(id) {
			this.setLineVal(0);
			this.setQsub(2);
		},
		removeLine: function(id) {
			this.setLineVal(0);
			if (this.qsub === 2) {
				this.setQsub(0);
			}
		},
		removeLineAndQsub: function(id) {
			this.setLineVal(0);
			this.setQsub(0);
		},

		//---------------------------------------------------------------------------
		// border.isBorder()     Border
		// border.setBorder()    Border
		// border.removeBorder() Border
		//---------------------------------------------------------------------------
		isBorder: function() {
			return this.ques > 0 || this.qans > 0;
		},
		setBorder: function() {
			if (this.puzzle.editmode) {
				this.setQues(1);
				this.setQans(0);
			} else if (this.ques !== 1) {
				this.setQans(1);
			}
		},
		removeBorder: function() {
			if (this.puzzle.editmode) {
				this.setQues(0);
				this.setQans(0);
			} else if (this.ques !== 1) {
				this.setQans(0);
			}
		},

		//---------------------------------------------------------------------------
		// border.isVert()  Border()
		// border.isHorz()  Border()
		//---------------------------------------------------------------------------
		isVert: function() {
			return this.isvert;
		},
		isHorz: function() {
			return !this.isvert;
		},

		//---------------------------------------------------------------------------
		// border.checkStableLine()  or 
		// border.isLineEX() border
		// border.isLineNG() border
		//---------------------------------------------------------------------------
		// [pipelink, loopsp], [barns, slalom, reflect, yajirin]
		checkStableLine: function(num) {
			// border.setLine
			if (this.enableLineNG) {
				return num !== 0 && this.isLineNG();
			}
			return false;
		},

		// Check if adding a line would create a corner in the side cells.
		checkFormCurve: function(num) {
			if (num === 0) {
				return false;
			}
			for (var i = 0; i <= 1; i++) {
				if (
					this.isVert() &&
					(this.sidecell[i].adjborder.top.isLine() ||
						this.sidecell[i].adjborder.bottom.isLine())
				) {
					return true;
				}
				if (
					!this.isVert() &&
					(this.sidecell[i].adjborder.left.isLine() ||
						this.sidecell[i].adjborder.right.isLine())
				) {
					return true;
				}
			}
			return false;
		},

		// cell.setQues => setCombinedLine (exist->ex)
		//  -> cellidnull
		isLineEX: function() {
			return false;
		},
		// border.setLineCal => checkStableLine
		//  -> cellidnull
		isLineNG: function() {
			var cell1 = this.sidecell[0],
				cell2 = this.sidecell[1];
			return this.isVert()
				? cell1.noLP(cell1.RT) || cell2.noLP(cell2.LT)
				: cell1.noLP(cell1.DN) || cell2.noLP(cell2.UP);
		}
	},

	//---------------------------------------------------------------------------
	// ExCell BoardExCell
	//---------------------------------------------------------------------------
	// (4)
	// ExCell
	"ExCell:BoardPiece": {
		group: "excell",

		adjacent: {}, // 

		//-----------------------------------------------------------------------
		// excell.getNum()     Cell
		// excell.setNum()     Cell
		// excell.noNum()      Cell
		//-----------------------------------------------------------------------
		getNum: function() {
			return this.qnum;
		},
		setNum: function(val) {
			this.setQnum(val);
		},
		noNum: function() {
			return !this.isnull && this.qnum === -1;
		},

		//---------------------------------------------------------------------------
		// excell.is51cell()   []
		//---------------------------------------------------------------------------
		is51cell: function() {
			return this.ques === 51;
		},

		//---------------------------------------------------------------------------
		// excell.ice() 
		//---------------------------------------------------------------------------
		ice: function() {
			return false;
		}
	}
});
